@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');
@import url('https://fonts.cdnfonts.com/css/unbounded');

.main-section {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background: #f4f4f4;
    padding: 2rem;
    /* font-family: 'Poppins', sans-serif; */
    font-family: 'Unbounded', sans-serif;                                                
    color: #333;
}

.main-heading {
    text-align: center;
    color: #82A0F4;
    font-size: 3rem;
    margin-bottom: 2rem;
    font-weight: 600;
    color: #000;
}

.fellowship-section {
    margin-bottom: 3rem;
}

.section-heading {
    font-size: 2.5rem;
    color: #82A0F4;
    border-bottom: 3px solid #000;
    padding-bottom: 0.5rem;
    margin-bottom: 1.5rem;
    font-weight: 600;
}

.card-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
}

.party-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    border: 1px solid #dcdcdc;
    border-radius: 8px;
    padding: 1.5rem;
    width: calc(33.333% - 1rem);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.party-card:hover {
    transform: translateY(-15px);
}

.party-logo {
    width: 150px;
    height: 150px;
    margin-bottom: 1rem;
    border-radius: 5px;
}

.party-heading {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 0.5rem;
    font-weight: 600;
    text-align: center;
}

.party-link {
    display: inline-block;
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: #82A0F4;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    text-align: center;
    margin-top: 1rem;
}

.party-link:hover {
    background-color: #6b8dd6;
}

@media (max-width: 768px) {
    .party-card {
        width: 100%;
    }

    .main-heading {
        font-size: 2.5rem;
    }

    .section-heading {
        font-size: 2rem;
    }

    .party-heading {
        font-size: 1.5rem;
    }

    .party-link {
        font-size: 0.9rem;
        padding: 0.4rem 0.8rem;
    }
}
